
import React, { Suspense, useState, useEffect } from 'react';
import Breakpoint_Controls from "./Misc_Handlers/Breakpoint_Controls.js"
import Variables_Display from "./Variable_Handlers/Variables_Display.js";
import Sensor_Selector from "./Sensor_Handlers/Sensor_Selector.js";
import Sensor_Display from "./Sensor_Handlers/Sensor_Display.js";
import Code_Display from "./Code_Handlers/Code_Display.js";
import Code_Selector from "./Code_Handlers/Code_Selector.js";
import Code_Updater from "./Code_Handlers/Code_Updater.js";
import BBC_Board from "../Boards/BBCMicrobit/Board.js"
import BBC_BoardOverlay from "../Boards/BBCMicrobit/Board_Overlay.js"
import BBC_Data from "../Boards/BBCMicrobit/data.js"
import CPE_Data from "../Boards/PlaygroundExpress/data.js"
import useWindowDimensions from './WindowDimensions.js';





/**
 *  This component holds the Live Debugger UI (determined by the board chosen by the user). This component requires two data parameters (pinData, deviceData), which hold
 * the current states of the analog/digital pins (pinData) and the states of onboard sensors (deviceData). This component also accepts a serial communication method, which should be
 * (queryBoard(selection, data)), to query the board for new data or update the boards pins/sensors.
 * @param {*} param0 
 */
function Layout({board, serialCom, deviceData}) {
  const [hold, setHold] = useState(false);
  const [selectedSensor, setSelectedSensor] = useState("LED");
  const [displayCodeUploader, setDisplayCodeUploader] = useState(false);
  const [selectedPin, setSelectedPin] = useState(CPE_Data.pins[0]);
  const [boardInfo, setBoardInfo] = useState(CPE_Data);
  const [code, setCode] = useState(null);
  const [breakpointList, setBreakpointList] = useState(null);
  const [selectedDataView, setSelectedDataView] = useState("HARDWARE");
  const [BoardGraphic, setBoardSVG] = useState(React.lazy(() => import(`../Boards/PlaygroundExpress/Board.js`)));//<BBC_Board x={displayMode ==="Basic" ? 562.5 : 15} y={displayMode ==="Basic" ? 165 : 0} width={displayMode ==="Basic" ? 600 : 450} height={displayMode ==="Basic" ? 600 : 450} />);
  const [BoardOverlay, setBoardOverlay] = useState(React.lazy(() => import(`../Boards/PlaygroundExpress/Board_Overlay.js`)));//<BBC_BoardOverlay x={displayMode ==="Basic" ? 562.5 : 15} y={displayMode ==="Basic" ? 165 : 0} width={displayMode ==="Basic" ? 600 : 450} height={displayMode ==="Basic" ? 600 : 450} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} serialCom={serialCom} deviceData ={deviceData} />);
  const { height, width } = useWindowDimensions();
  
  useEffect(()=>{
    setBoardSVG(React.lazy(() => import(`../Boards/${board}/Board.js`)));//.then(BoardGraphic => setBoardSVG()).catch(err=> console.log("Unable to load SVG for board: " + board));
    setBoardOverlay(React.lazy(() => import(`../Boards/${board}/Board_Overlay.js`)));//.then(BoardOverlay => setBoardOverlay()).catch(err=> console.log("Unable to load overlay for board: " + board));
    import(`../Boards/${board}/data.js`)
        .then(({default: data}) => {setSelectedPin(data.pins[0]); setBoardInfo(data);})
        .catch(err=> console.log("Unable to load data for board: " + board));
  }, [board]);

  useEffect(() =>{//Add modifyier warning when pin changes
    if(selectedSensor !== "Pins" && selectedSensor !== "Led Matrix")
    {
       if(selectedSensor.startsWith("E_"))
        {
            serialCom("EXTERNAL_SENSOR", {name: selectedSensor.substr(2)});
        }
        else
        {
            serialCom(selectedSensor, {});
        }
    }
  }, [selectedSensor]);

  useEffect(()=>{
    setDisplayCodeUploader(false);
  }, [code]);

  let codeDisplay =<Code_Updater x={680} y={55} height={425} updateCode={setCode} />;
  if(!displayCodeUploader && code !== null)
  {
      codeDisplay = <>
                        <Code_Display x={675} y={57.5} width={745} height={590} code_package={code} serialCom={serialCom} breakpoint_id={deviceData.breakpoints.length > 0 ? deviceData.breakpoints[deviceData.breakpoints.length - 1] : "NA"}/>
                        {/* The code display's height is too long, the following rect & line act as a mask to cover the bottom portion of the code */}
                        <rect  x={672} y={647} width={750} height={25}  fill="white" strokeWidth={4} />
                        <line  x1={671.25} y1={651.75} x2={1423.25} y2={651.75} stroke="var(--bgColor1)" strokeLinecap="butt" strokeWidth={4} rx={5} ry={5}/>  
                    </>;
  }

    let sensor_list = [ "Pins", "LED", "Button",...boardInfo.onboard_sensors, ...deviceData.external_sensor_names].filter(x => x !== null);
    let sensorOptions = [];
    for(let i = 0; i < sensor_list.length; i++)
    {
        sensorOptions.push(sensor_list[i] === selectedSensor ?<option value={`${i}`} selected>{sensor_list[i]}</option> : <option value={`${i}`}>{sensor_list[i]}</option>);
    }

  function mHover(class_name, attribute, colorA, colorB){
    document.querySelectorAll("." + class_name).forEach(element =>{
            element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
        }
    );
  }

  function changeSensor()
  {
    var sensorSelect = document.getElementById("sensor_selector_dropdown");
    var value = sensorSelect.options[sensorSelect.selectedIndex].text;
    sensorSelect.selectedIndex = 0;
    if(value !== "Select Sensor")
    {
        setSelectedSensor(value);
    }
  }
  //Determine formatting based on wide/mobile screen sizing
  let isWideScreen =  width >= 1024;
  return (
      <>
        <svg 
            width={"100%"} 
            /* Research how to make this more responsive/better width & height values width < 700 ? "0 0 720 1100" : width < 1024 ? "0 0 720 760" : "0 0 1440 720" */
            viewBox={`0 0 ${width} ${height}`}
            x={0}
            y={height * 0.08}
            preserveAspectRatio="xMinYMin"
        >  
            <>{/* Wide Screen Layout */}
                {/* Microcontroller Display */}
                <Suspense fallback={<div>Loading...</div>}>
                    <BoardGraphic x={0} y={(width *0.0027)} width={width *0.27} height={(width *0.27) * 0.8} />
                    <BoardOverlay x={0} y={(width *0.0027)} width={width *0.27} height={(width *0.27) * 0.8} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} serialCom={serialCom} deviceData ={deviceData} />
                </Suspense>

                {/* Sensor Selector */}
                <rect x={width * 0.01} y={(height * 0.455)} width={width *0.25} height={height * 0.1} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01} />
                <foreignObject x={width * 0.035} y={(height * 0.47)} width={width *0.20} height={height * 0.1}> 
                    <select id="sensor_selector_dropdown" style={{width:"100%", fontSize:`${(width * 0.002)}em`, fontWeight: "bolder", borderColor:"var(--bgColor1)"}} onChange={()=>changeSensor()}>
                        {sensorOptions}
                    </select>
                </foreignObject>

                {/* Pause/Resume Code Controls */}
                <rect x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="white" stroke="var(--bgColor1)" strokeWidth={height * 0.01} rx={width * 0.01} ry={width * 0.01}/>
                <rect className="pause_code_button" x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="white"  rx={width * 0.01} ry={width * 0.01}/>
                <rect x={width * 0.035} y={(height * 0.5925)} width={width *0.2} height={height * 0.284} fill="white"  rx={width * 0.01} ry={width * 0.01}/>
                
                    <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.6595)} alignmentBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:`${width * 0.002}em`, fontWeight:"bold"}}>
                            Click to
                    </text>                    
                    {!hold ? 
                        <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.7345)} alignmentBaseline="middle" textAnchor="middle" fill="red" style={{fontSize:`${width * 0.002}em`, fontWeight:"bold"}}>
                                Pause
                        </text> : 
                        <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.7345)} alignmentBaseline="middle" textAnchor="middle" fill="green" style={{fontSize:`${width * 0.002}em`, fontWeight:"bold"}}>
                                Resume
                        </text>
                    }
                    <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.8095)} alignmentBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:`${width * 0.002}em`, fontWeight:"bold"}}>
                            Your Code
                    </text>
                <rect x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="transparent" rx={width * 0.01} ry={width * 0.01} cursor="pointer" onMouseEnter={()=>{mHover("pause_code_button", "fill", "var(--bgColor1)", "white")}} onMouseLeave={()=>{mHover("pause_code_button", "fill", "var(--bgColor1)", "white")}} onClick={() => {setHold(hold => {hold ? serialCom("RELEASE", {}) : serialCom("HOLD", {}); return !hold;})} } />
                
                {/* Sensor Display */}
                <rect x={(width*0.27)} y={0} width={width * 0.73}  height={(height * 0.45)} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01}>
                    <title>Data Display</title>
                </rect> 
                <Sensor_Display x={(width*0.265)} y={0} width={width * 0.73} height={(height * 0.45)} serialCom={serialCom} deviceData={deviceData} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} modeText={boardInfo.modeText} modes={boardInfo.modes} pinList={boardInfo.pins} isWideScreen={isWideScreen}/>
                
                {/* Software Controls */}
                <rect x={(width*0.27)} y={(height * 0.455)} width={width * 0.73}  height={(height * 0.45)} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01}>
                    <title>Software Controls</title>
                </rect>
            </> 
           
        </svg>
     </>

    );
}

export default Layout