

import React, { useState, useEffect } from 'react';
//Todo implement download feature for Gestures
function Gesture({ x, y, width, gesture, displayMode, download}) {
    const [gestureType, setGestureType] = useState("All");
    const [gestureList, setGestureList] = useState(displayMode === "Basic" ? ["","","","","","",""] : ["","","","",""]);//7 Gestures in Basic, 5 for Advance 
    let force_values = ["Eight G", "Six G", "Three G", "Shake", "Free Fall"]
    let orientation_values = ["Logo Down", "Logo Up", "Screen Down", "Screen Up", "Tilt Left", "Tilt Right"]
    useEffect(() =>{//If gesture type updates, reset the current list of gestures
        if(gesture !== undefined)
        {
            if(gesture === "none" || 
              (gestureType === "Force" && force_values.includes(gesture)) || 
              (gestureType === "Orientation" && orientation_values.includes(gesture)) || 
              (gestureType === "All" && (force_values.includes(gesture) || orientation_values.includes(gesture))))
            {
                setGestureList(displayMode === "Basic" ? [gesture,"","","","","",""] : [gesture,"","","",""]);
            }
            else
            {
                setGestureList(displayMode === "Basic" ? ["","","","","","",""] : ["","","","",""]);
            }
        }
        else
        {
            setGestureList(displayMode === "Basic" ? ["","","","","","",""] : ["","","","",""]);
        }
        
    }, [gestureType])

    useEffect(() =>{//Only update the gesture list when a new gesture has been provided
        let list_copy = gestureList;
        if(gesture !== undefined)
        {
            if(gesture === "none" || 
              (gestureType === "Force" && force_values.includes(gesture)) || 
              (gestureType === "Orientation" && orientation_values.includes(gesture)) || 
              (gestureType === "All" && (force_values.includes(gesture) || orientation_values.includes(gesture))))
            {
                for(let i = gestureList.length - 1; i > 0; i--)
                {
                    list_copy[i] = list_copy[i - 1];
                }
                list_copy[0] = gesture;
                setGestureList(list_copy);
            }
        }
    }, [gesture])
    
    
    let gesture_image = [];
    for(let i = 0; i < gestureList.length; i++)
    {
        gesture_image.push(<rect  x={275 + (97 * i)} y={25} height={100} width={97} fill="white" stroke="black" strokeWidth="2" />);
        if(gestureList[i] !== "")
        {
            if(gestureList[i].includes(" "))
            {
                gesture_image.push(<text x={275 + (97 * i) + 48.5} y={70} dominantBaseline="middle" textAnchor="middle" fill={ "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>{gestureList[i].substr(0, gestureList[i].indexOf(" "))}</text>);
                gesture_image.push(<text x={275 + (97 * i) + 48.5} y={90} dominantBaseline="middle" textAnchor="middle" fill={ "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>{gestureList[i].substr(gestureList[i].indexOf(" ") + 1)}</text>);
            }
            else{
                gesture_image.push(<text x={275 + (97 * i) + 48.5} y={75} dominantBaseline="middle" textAnchor="middle" fill={ "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>{gestureList[i]}</text>);
            }
        }
    }
    
    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    return (
      <>
        <svg 
            width={width} 
            viewBox={displayMode === "Basic" ? "0 0 975 150": "0 0 785 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        {/* Logo */}  
        {displayMode === "Basic" ?<>
        <circle cx={55} cy={47.5} r={20} fill="white" />
        <rect x={35} y={40} width={40} height={50} rx={5} ry={5} fill="white"/>
        <line x1={45} y1={80} x2={25} y2={60} strokeWidth={15} strokeLinecap="round" stroke="white"/>
        <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
        <text x={50} y={112.5} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"1.5em", fontWeight:"bolder"}}>Gesture</text>
        </> : <></>}
        <g transform = {displayMode === "Basic" ? "" : "translate(-80)"}>
            {/* All Gestures*/}
            <rect className="gesture_all_select" x={125} y={25} height={30} width={125} fill={gestureType === "All" ? "black": "white"} stroke="black" strokeWidth="2" />
            <text className="gesture_all_select"  x={187.5} y={42.5} dominantBaseline="middle" textAnchor="middle" fill={gestureType === "All" ? "white": "black"}  style={{fontSize:"1.5em", fontWeight:"bold"}}>All</text>
            <rect x={125} y={25} height={30} width={125} fill="transparent" style={{cursor:gestureType === "All" ? "default" :"pointer"}} onMouseEnter={()=>{if(gestureType !== "All"){mHover("gesture_all_select", "fill", "black", "white")}}} onMouseLeave={()=>{if(gestureType !== "All"){mHover("gesture_all_select", "fill", "black", "white")}}} onClick={()=>{setGestureType("All")}}/>
            
            {/* Orientation Gestures*/}
            <rect className="orientation_select" x={125} y={60} height={30} width={125} fill={gestureType === "Orientation" ? "black": "white"} stroke="black" strokeWidth="2" />
            <text className="orientation_select" x={187.5} y={77.5} dominantBaseline="middle" textAnchor="middle" fill={gestureType === "Orientation" ? "white": "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>Orientation</text>
            <rect x={125} y={60} height={30} width={125} fill="transparent" style={{cursor:gestureType === "Orientation" ? "default" :"pointer"}} onMouseEnter={()=>{if(gestureType !== "Orientation"){mHover("orientation_select", "fill", "black", "white")}}} onMouseLeave={()=>{if(gestureType !== "Orientation"){mHover("orientation_select", "fill", "black", "white")}}} onClick={()=>{setGestureType("Orientation")}}/>


            {/* Force Gestures*/}
            <rect className="force_select" x={125} y={95} height={30} width={125} fill={gestureType === "Force" ? "black": "white"} stroke="black" strokeWidth="2" />
            <text className="force_select" x={187.5} y={112.5} dominantBaseline="middle" textAnchor="middle" fill={gestureType === "Force" ? "white": "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>Force</text>
            <rect x={125} y={95} height={30} width={125} fill="transparent" style={{cursor:gestureType === "Force" ? "default" :"pointer"}} onMouseEnter={()=>{if(gestureType !== "Force"){mHover("force_select", "fill", "black", "white")}}} onMouseLeave={()=>{if(gestureType !== "Force"){mHover("force_select", "fill", "black", "white")}}} onClick={()=>{setGestureType("Force")}}/>

            {/*Gesture Carosel*/}
            <rect  x={275} y={25} height={100} width={485} fill="white" stroke="black" strokeWidth="2" />
            {gesture_image}
        </g>
        </svg> 
     </>

    );
}

export default Gesture