
import React, { useState, useEffect } from 'react';
import SmoothLineGraph from "../GraphingUtilities/SmoothLineGraph.js";

function GenericSensor({sensor_name, download, logo, x, y, width, reading, min=0, max=1023, font_size="2.25em", displayMode}) {
    const [maxVal, setMaxVal] = useState(null);
    const [minVal, setMinVal] = useState(null);

    useEffect(()=>{
        setMaxVal(Math.max(...reading));
        setMinVal(Math.min(...reading));
    }, [sensor_name]);
    let graph = <></>;
    if(reading.length > 0)
    {
        if(maxVal === null){
            setMaxVal(reading[0])
        }
        else if(reading[reading.length - 1] > maxVal)
        {
            setMaxVal(reading[reading.length - 1]);
        }
        if(minVal === null){
            setMinVal(reading[0])
        }
        else if(reading[reading.length - 1] < minVal)
        {
            setMinVal(reading[reading.length - 1]);
        }
        graph = <SmoothLineGraph x={300} y={25.5} height={99} download={()=>download(sensor_name)} width={275} x_axis_offset={10} y_axis_offset={12.5} data={reading.slice(-25)} data_max={maxVal} data_min={minVal} reset_min_max={reset_min_max}/>
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    function reset_min_max(){
        setMaxVal(reading.length > 0 ? Math.max(...reading.slice(-25)) : 0);
        setMinVal(reading.length > 0 ? Math.min(...reading.slice(-25)) : 0);
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={ "0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        {/* Logo 
        {logo}
        <line x1={110} x2={110} y1={25} y2={125} stroke="white" strokeWidth="4" />
        <text x={52.5} y={112.5} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:font_size, fontWeight:"bolder"}}>{sensor_name}</text>
*/}  
        <g transform="translate(-100)">
            {/* Current Value*/}
            <rect x={125} y={25} height={65} width={100} fill="white" stroke="black" strokeWidth="2" />
            <text x={175} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize: "2em", fontWeight:"bold"}}>{reading.length > 0 ? reading[reading.length-1] !== undefined ? reading[reading.length-1].toFixed(2) : 0 : "NA"}</text>
            <rect x={125} y={90} height={35} width={100} fill="white" stroke="black" strokeWidth="2" />
            <text  x={175} y={110} dominantBaseline="middle" textAnchor="middle" fill={"black"}  style={{fontSize:"1.5em", fontWeight:"bold"}}>Value</text>
            
            {/*Data graph*/}
            <rect  x={230} y={25} height={100} width={355}  fill="white" stroke="black" strokeWidth="2" />
            {graph}
          </g>
        </svg> 
     </>

    );
}

export default GenericSensor